    // 一次多選座位
    onSelect(e) {
      let self = this;
      e.added.forEach((el) => {
        // el.classList.add("selected");
        let customId = el.id.split("seat.")[1];
        let data = _.find(self.AreaForm.cubes, ["customId", customId]);
